
'use client';

import React, { useEffect, useState } from 'react';
import { useAuth } from '@/context/AuthContext';
import { api } from '@/services/api';
import {
    Package,
    Truck,
    CheckCircle2,
    Clock,
    LogOut,
    User as UserIcon,
    Phone,
    Mail,
    ChevronRight,
    Search,
    AlertCircle,
    ShoppingBag
} from 'lucide-react';
import { useRouter } from 'next/navigation';
import { Header } from '@/components/Header';
import { Footer } from '@/components/Footer';
import Link from 'next/link';
import { cn } from '@/lib/utils';
import { format } from 'date-fns';
import { fr } from 'date-fns/locale';

export default function AccountPage() {
    const { user, logout, loading: authLoading } = useAuth();
    const [orders, setOrders] = useState<any[]>([]);
    const [loading, setLoading] = useState(true);
    const router = useRouter();

    useEffect(() => {
        if (!authLoading && !user) {
            router.push('/login?redirect=/account');
        }

        if (user) {
            api.getOrders(user.id)
                .then(res => setOrders(res.orders))
                .catch(err => console.error('Error fetching orders:', err))
                .finally(() => setLoading(false));
        }
    }, [user, authLoading, router]);

    const handleLogout = () => {
        logout();
        router.push('/');
    };

    if (authLoading || (loading && !orders.length)) {
        return (
            <div className="min-h-screen bg-[#F4F4F0] flex items-center justify-center">
                <div className="flex flex-col items-center gap-4">
                    <Package className="w-12 h-12 text-indigo-600 animate-bounce" />
                    <p className="text-slate-500 font-black uppercase tracking-widest text-xs">Chargement de votre compte...</p>
                </div>
            </div>
        );
    }

    const getStatusInfo = (status: string) => {
        switch (status.toLowerCase()) {
            case 'paid':
                return { label: 'Payé', color: 'text-green-600 bg-green-50', icon: CheckCircle2 };
            case 'delivered':
                return { label: 'Livré', color: 'text-blue-600 bg-blue-50', icon: Truck };
            case 'cancelled':
                return { label: 'Annulé', color: 'text-red-600 bg-red-50', icon: AlertCircle };
            default:
                return { label: 'En attente', color: 'text-orange-600 bg-orange-50', icon: Clock };
        }
    };

    return (
        <div className="min-h-screen bg-[#F4F4F0]">
            <Header />

            <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-12">
                <div className="grid lg:grid-cols-12 gap-8 items-start">

                    {/* Sidebar / Profile Info */}
                    <div className="lg:col-span-4 space-y-6">
                        <div className="bg-white rounded-[2.5rem] p-8 shadow-xl shadow-slate-200/50 border border-slate-100 overflow-hidden relative group">
                            <div className="absolute top-0 right-0 p-6 opacity-5 group-hover:scale-110 transition-transform">
                                <UserIcon className="w-32 h-32" />
                            </div>

                            <div className="relative z-10">
                                <div className="w-20 h-20 bg-indigo-600 rounded-3xl flex items-center justify-center text-white mb-6 shadow-lg shadow-indigo-600/20">
                                    <UserIcon className="w-10 h-10" />
                                </div>
                                <h2 className="text-2xl font-black text-slate-900 tracking-tight">{user?.name}</h2>
                                <div className="mt-4 space-y-3">
                                    <div className="flex items-center gap-3 text-slate-500 font-medium text-sm">
                                        <Phone className="w-4 h-4" />
                                        {user?.phone}
                                    </div>
                                    {user?.email && (
                                        <div className="flex items-center gap-3 text-slate-500 font-medium text-sm">
                                            <Mail className="w-4 h-4" />
                                            {user?.email}
                                        </div>
                                    )}
                                </div>

                                <button
                                    onClick={handleLogout}
                                    className="mt-8 flex items-center gap-2 text-red-500 font-black text-xs uppercase tracking-widest hover:text-red-600 transition-colors w-full pt-6 border-t border-slate-50"
                                >
                                    <LogOut className="w-4 h-4" />
                                    Se déconnecter
                                </button>
                            </div>
                        </div>

                        {/* Quick Stats? */}
                        <div className="bg-indigo-600 rounded-[2.5rem] p-8 text-white shadow-xl shadow-indigo-600/20">
                            <p className="text-white/60 text-[10px] font-black uppercase tracking-widest">Total des commandes</p>
                            <div className="flex items-baseline gap-2 mt-2">
                                <span className="text-5xl font-black tracking-tighter">{orders.length}</span>
                                <span className="text-sm font-bold opacity-60 uppercase">Commandes</span>
                            </div>
                        </div>
                    </div>

                    {/* Order History */}
                    <div className="lg:col-span-8 space-y-6">
                        <div className="flex items-center justify-between mb-2">
                            <h2 className="text-2xl font-black text-slate-900 tracking-tight">Historique des Commandes</h2>
                            <span className="text-[10px] font-black uppercase tracking-[0.2em] text-slate-400">Mes Achats</span>
                        </div>

                        {orders.length === 0 ? (
                            <div className="bg-white rounded-[2.5rem] p-16 text-center shadow-xl shadow-slate-200/50 border border-slate-100 italic">
                                <div className="w-24 h-24 bg-slate-50 rounded-full flex items-center justify-center mx-auto mb-6">
                                    <ShoppingBag className="w-12 h-12 text-slate-200" />
                                </div>
                                <p className="text-slate-400 font-bold mb-6">Vous n'avez pas encore passé de commande.</p>
                                <Link href="/" className="bg-indigo-600 text-white px-8 py-4 rounded-2xl font-black text-sm uppercase tracking-widest hover:scale-105 transition-all inline-block shadow-lg shadow-indigo-600/20 not-italic">
                                    Commencer mes achats
                                </Link>
                            </div>
                        ) : (
                            <div className="grid gap-4">
                                {orders.map((order) => {
                                    const { label, color, icon: Icon } = getStatusInfo(order.status);
                                    return (
                                        <Link
                                            key={order.id}
                                            href={`/payment/${order.id}`}
                                            className="bg-white rounded-[2rem] p-6 shadow-sm hover:shadow-xl hover:shadow-slate-200/50 border border-slate-100 transition-all group flex flex-col md:flex-row md:items-center gap-6"
                                        >
                                            <div className="flex-1 space-y-1">
                                                <div className="flex items-center gap-3 mb-1">
                                                    <span className="text-[10px] font-black text-slate-400 uppercase tracking-widest">Commande #{order.id.slice(0, 8)}</span>
                                                    <span className={cn("px-3 py-1 rounded-full text-[10px] font-black uppercase tracking-wider flex items-center gap-1.5", color)}>
                                                        <Icon className="w-3 h-3" />
                                                        {label}
                                                    </span>
                                                </div>
                                                <h3 className="text-lg font-black text-slate-900">
                                                    {Number(order.total).toLocaleString()} <span className="text-sm font-bold opacity-40">FCFA</span>
                                                </h3>
                                                <p className="text-xs text-slate-400 font-medium">
                                                    Passée le {format(new Date(order.created_at), "d MMMM yyyy 'à' HH:mm", { locale: fr })}
                                                </p>
                                            </div>

                                            <div className="flex items-center justify-between md:justify-end gap-6 border-t md:border-none pt-4 md:pt-0">
                                                <div className="md:text-right">
                                                    <p className="text-[10px] font-black text-slate-400 uppercase mb-1">Livraison à</p>
                                                    <p className="text-xs font-bold text-slate-700 truncate max-w-[150px]">{order.address_full}</p>
                                                </div>
                                                <div className="w-10 h-10 bg-slate-50 rounded-xl flex items-center justify-center group-hover:bg-indigo-600 group-hover:text-white transition-all">
                                                    <ChevronRight className="w-5 h-5" />
                                                </div>
                                            </div>
                                        </Link>
                                    );
                                })}
                            </div>
                        )}
                    </div>

                </div>
            </main>

            <Footer />
        </div>
    );
}
